<template>
  <div class="history-monitor-container">
    <div class="page-title"></div>

    <!--  -->
    <div class="query-form">
      <!--  -->
      <el-select
          v-model="plantationId"
          placeholder=""
          clearable
          class="query-item"
          @change="fetchData"
      >
        <el-option
            v-for="item in plantationList"
            :key="item.plantation_id"
            :label="item.plantation_name"
            :value="item.plantation_id"
        ></el-option>
      </el-select>

      <!--  -->
      <div class="manual-time-range">
        <el-date-picker
            v-model="startTime"
            type="datetime"
            placeholder=""
            class="time-input"
            format="YYYY-MM-DD HH:mm:ss"
            value-format="YYYY-MM-DD HH:mm:ss"
        />
        <span class="range-separator"></span>
        <el-date-picker
            v-model="endTime"
            type="datetime"
            placeholder=""
            class="time-input"
            format="YYYY-MM-DD HH:mm:ss"
            value-format="YYYY-MM-DD HH:mm:ss"
        />
      </div>

      <!--  -->
      <el-select
          v-model="dataType"
          placeholder=""
          clearable
          class="query-item"
          @change="handleDataTypeSwitch"
      >
        <el-option label="" value="weather"></el-option>
        <el-option label="" value="soil"></el-option>
      </el-select>

      <!--  soil  -->
      <el-select
          v-if="dataType === 'soil'"
          v-model="sensorType"
          placeholder=""
          clearable
          class="query-item"
      >
        <el-option label="" value="moisture"></el-option>
        <el-option label="" value="temperature"></el-option>
        <el-option label="pH " value="ph"></el-option>
        <el-option label="" value="nutrient"></el-option>
        <el-option label="" value="ec"></el-option>
      </el-select>

      <!--  -->
      <el-button type="primary" @click="handleQuery" class="query-btn">
        <i class="el-icon-search"></i> 
      </el-button>

      <!--  -->
      <el-button @click="handleReset" class="reset-btn">
        <i class="el-icon-refresh"></i> 
      </el-button>
    </div>

    <!--  -->
    <el-loading
        v-if="loading"
        text="..."
        background="rgba(255, 255, 255, 0.7)"
    ></el-loading>

    <!--  -->
    <div v-if="!loading" class="data-display">
      <div v-if="tableData.length === 0 && queryExecuted" class="empty-state">
        <el-empty description=""></el-empty>
      </div>

      <!--  -->
      <template v-if="dataType === 'weather'">
        <el-table
            v-if="tableData.length > 0"
            :data="tableData"
            border
            stripe
            :header-cell-style="{ background: '#f5f7fa' }"
            class="weather-table"
        >
          <el-table-column
              prop="timestamp"
              label=""
              width="220"
          ></el-table-column>
          <el-table-column
              label=""
          >
            <template #default="scope">
              {{ scope.row.temperature.value }} {{ scope.row.temperature.unit }}
            </template>
          </el-table-column>
          <el-table-column
              label=""
          >
            <template #default="scope">
              {{ scope.row.humidity.value }} {{ scope.row.humidity.unit }}
            </template>
          </el-table-column>
          <el-table-column
              label=""
          >
            <template #default="scope">
              {{ scope.row.light.value }} {{ scope.row.light.unit }}
            </template>
          </el-table-column>
          <el-table-column
              label=""
          >
            <template #default="scope">
              {{ scope.row.wind.speed.value }} {{ scope.row.wind.speed.unit }}
            </template>
          </el-table-column>
          <el-table-column
              label=""
          >
            <template #default="scope">
              {{ scope.row.wind.direction.value }} {{ scope.row.wind.direction.unit }}{{ scope.row.wind.direction.description }}
            </template>
          </el-table-column>
          <el-table-column
              label=""
          >
            <template #default="scope">
              {{ scope.row.rainfall.value }} {{ scope.row.rainfall.unit }}{{ scope.row.rainfall.duration }}
            </template>
          </el-table-column>
          <el-table-column
              label="ID"
              prop="device.id"
          ></el-table-column>
          <el-table-column
              label=""
              prop="device.type"
          ></el-table-column>
          <el-table-column
              label=""
              prop="device.location.name"
          ></el-table-column>
          <el-table-column
              label=""
              prop="device.status"
          ></el-table-column>
          <el-table-column
              label=""
              prop="device.last_calibration"
          ></el-table-column>
        </el-table>
      </template>

      <!--  -->
      <template v-else-if="dataType === 'soil'">
        <el-table
            :data="tableData"
            border
            stripe
            :header-cell-style="{ background: '#f5f7fa' }"
            class="soil-table"
        >
          <el-table-column
              prop="timestamp"
              label=""
              width="220"
          ></el-table-column>
          <el-table-column
              :label="sensorLabel"
              prop="value"
          ></el-table-column>
          <el-table-column
              prop="unit"
              label=""
          ></el-table-column>
          <el-table-column
              prop="sensor_id"
              label=" ID"
          ></el-table-column>
          <el-table-column
              v-show="showMoisture"
              prop="soil_moisture.value"
              label=""
          ></el-table-column>
          <el-table-column
              v-show="showTemperature"
              prop="soil_temperature.value"
              label=""
          ></el-table-column>
          <el-table-column
              v-show="showPh"
              prop="soil_ph.value"
              label=""
          ></el-table-column>
          <el-table-column
              v-show="showNutrient"
              label="(N/P/K)"
          >
            <template #default="scope">
              N: {{ scope.row.soil_nutrients.nitrogen.value }}<br />
              P: {{ scope.row.soil_nutrients.phosphorus.value }}<br />
              K: {{ scope.row.soil_nutrients.potassium.value }}
            </template>
          </el-table-column>
          <el-table-column
              v-show="showEc"
              prop="soil_electrical_conductivity.value"
              label=""
          ></el-table-column>
        </el-table>
      </template>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue';
import {
  ElMessage,
  ElEmpty,
  ElButton,
  ElTable,
  ElTableColumn,
  ElSelect,
  ElOption,
  ElLoading,
  ElDatePicker
} from 'element-plus';
import axios from 'axios';

// 
const baseUrl = 'http://your-backend-api-base-url'; // 

// 
const plantationId = ref('');
const startTime = ref('');
const endTime = ref('');
const dataType = ref('');
const sensorType = ref('');
const tableData = ref([]);
const plantationList = ref([]);
const loading = ref(false);
const queryExecuted = ref(false);
const showMoisture = ref(false);
const showTemperature = ref(false);
const showPh = ref(false);
const showNutrient = ref(false);
const showEc = ref(false);

// 
const fetchPlantationList = async () => {
  try {
    const response = await axios.get(`${baseUrl}/api/plantations`);
    plantationList.value = response.data;
  } catch (error) {
    ElMessage.error('');
  }
};

// 
onMounted(() => {
  fetchPlantationList();
});

// 
const handleDataTypeSwitch = () => {
  sensorType.value = '';
  tableData.value = [];
  resetSoilFields();
};

const resetSoilFields = () => {
  showMoisture.value = false;
  showTemperature.value = false;
  showPh.value = false;
  showNutrient.value = false;
  showEc.value = false;
};

// 
const handleQuery = async () => {
  // 1. 
  if (!validateAllTime()) return;
  // 2. 
  if (!validateOtherParams()) return;

  loading.value = true;
  queryExecuted.value = true;

  // 
  const requestParams = {
    plantationId: plantationId.value,
    startTime: startTime.value,
    endTime: endTime.value,
    dataType: dataType.value,
    sensorType: sensorType.value,
  };

  try {
    //  /api/teaGarden/query
    const response = await axios.post(`${baseUrl}/api/teaGarden/query`, requestParams);
    //  { code: 200, data: { historical_weather: [...] } } 
    if (response.data.code === 200) {
      if (dataType.value === 'weather') {
        tableData.value = response.data.data.historical_weather;
      } else {
        // 
        tableData.value = response.data.data.historical_soil_data;
      }
    } else {
      ElMessage.warning('');
    }
  } catch (error) {
    ElMessage.error('');
    console.error(':', error);
  } finally {
    loading.value = false;
  }
};

// 
const validateAllTime = () => {
  const checkStart = validateTime(startTime.value, 'start')
  const checkEnd = validateTime(endTime.value, 'end')
  return checkStart && checkEnd
};

const validateTime = (val, type) => {
  if (!val) {
    ElMessage.warning('')
    return false
  }
  if (!/^\d{4}-\d{2}-\d{2} \d{2}:\d{2}:\d{2}$/.test(val)) {
    ElMessage.warning(`${type === 'start' ? '' : ''} YYYY-MM-DD HH:mm:ss `)
    return false
  }
  return true
};

// 
const validateOtherParams = () => {
  if (!plantationId.value) {
    ElMessage.warning('')
    return false
  }
  if (!dataType.value) {
    ElMessage.warning('')
    return false
  }
  if (dataType.value === 'soil' && !sensorType.value) {
    ElMessage.warning('')
    return false
  }
  return true
};

// 
const handleReset = () => {
  plantationId.value = '';
  startTime.value = '';
  endTime.value = '';
  dataType.value = '';
  sensorType.value = '';
  tableData.value = [];
  queryExecuted.value = false;
  resetSoilFields();
};

// 
const sensorLabel = computed(() => ({
  moisture: '',
  temperature: '',
  ph: '',
  nutrient: '(N/P/K)',
  ec: '',
}[sensorType.value] || ''))
</script>

<style scoped>
.history-monitor-container {
  padding: 20px;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.05);
  min-height: 500px;
}

.page-title {
  font-size: 18px;
  font-weight: bold;
  color: #333;
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid #eee;
}

.query-form {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  margin-bottom: 24px;
  align-items: center;
}

.query-item {
  width: 220px;
}

.manual-time-range {
  display: flex;
  align-items: center;
  gap: 8px;
}

.time-input {
  width: 220px;
}

.range-separator {
  color: #666;
}

.query-btn, .reset-btn {
  height: 40px;
  padding: 0 20px;
}

.data-display {
  margin-top: 16px;
}

.empty-state {
  margin: 50px 0;
  text-align: center;
}

.weather-table, .soil-table {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

/*  */
@media (max-width: 1200px) {
  .query-item, .time-input {
    width: 180px;
  }
}

@media (max-width: 992px) {
  .query-item, .time-input {
    width: 100%;
  }

  .query-btn, .reset-btn {
    width: 100%;
    margin-top: 8px;
  }
}
</style>