import { defineStore } from "pinia";
// import { verifyLocation } from "@/locationVerification";
export const useGardenStore = defineStore('garden',{
  state:()=>({
    // 
    gardenList: [
      {
        id: '1001',
        name: '',
        address: '',
        latitude: 27.76,
        longitude: 117.9,
        area: 80.5,
        variety: '',
        plantingYear: 2010,
        owner: '',
        certification: '',
        status: ''
      },
      {
        id: '1002',
        name: '',
        address: '',
        latitude: 21.98,
        longitude: 100.54,
        area: 120.3,
        variety: '',
        plantingYear: 2012,
        owner: '',
        certification: '',
        status: ''
      },
      {
        id: '1003',
        name: '',
        address: '',
        latitude: 30.23,
        longitude: 120.76,
        area: 50.8,
        variety: '',
        plantingYear: 2015,
        owner: '',
        certification: '',
        status: ''
      }
    ],
    // 
    dialogvisible:false,

    // =null=>!=null=>
    currentEditingId:null,

    // 
    isEditMode:false,
    tableLoading: false,
    formData: {
      name: '',
      address: '',
      latitude: null,
      longitude: null,
      area: null,
      variety: '',
      plantingYear: null,
      owner: '',
      status: ''
    },
    // 
    formRules: {
      name: [
        { required: true, message: '', trigger: 'blur' },
        { min: 2, max: 50, message: ' 2  50 ', trigger: 'blur' }
      ],
      address: [
        { required: true, message: '', trigger: 'blur' },
        { min: 5, message: ' 5 ', trigger: 'blur' }
      ],
      coordinates: [
        {
          validator: (rule, value, callback, source) => {
            const { latitude, longitude } = source
            if (latitude=='' || longitude=='') {
              callback(new Error(''))
            } else if (latitude < -90 || latitude > 90) {
              callback(new Error(' -90  90 '))
            } else if (longitude < -180 || longitude > 180) {
              callback(new Error(' -180  180 '))
            } else {
              callback()
            }
          },
          trigger: 'blur'
        }
      ],
      area: [
        { required: true, message: '', trigger: 'blur' },
        {
          validator: (rule, value, callback, source) => {
            if (value <= 0) {
              callback(new Error(' 0'))
            } else if (value > 10000) {
              callback(new Error(' 10000 '))
            } else if (source.variety && source.plantingYear) {
              const currentYear = new Date().getFullYear()
              const age = currentYear - source.plantingYear

              const varietyAreaRanges = {
                '': { min: 0.5, max: 200 },
                '': { min: 0.3, max: 150 },
                '': { min: 0.2, max: 100 },
                '': { min: 0.5, max: 300 },
                '': { min: 1, max: 500 },
                'default': { min: 0.1, max: 10000 }
              }

              const range = varietyAreaRanges[source.variety] || varietyAreaRanges.default

              if (age < 3 && value > range.max * 0.5) {
                callback(new Error(` ${range.max * 0.5} `))
              } else if (value < range.min || value > range.max) {
                callback(new Error(`${source.variety}  ${range.min} - ${range.max} `))
              } else {
                callback()
              }
            } else {
              callback()
            }
          },
          trigger: 'blur'
        }
      ],
      variety: [
        { required: true, message: '', trigger: 'blur' }
      ],
      plantingYear: [
        { required: true, message: '', trigger: 'blur' },
        {
          validator: (rule, value, callback) => {
            const currentYear = new Date().getFullYear()
            if (value < 1950) {
              callback(new Error(' 1950 '))
            } else if (value > currentYear) {
              callback(new Error(` ${currentYear} `))
            } else {
              callback()
            }
          },
          trigger: 'blur'
        }
      ],
      owner: [
        { required: true, message: '', trigger: 'blur' },
        { min: 2, message: ' 2 ', trigger: 'blur' }
      ],
      status: [
        { required: true, message: '', trigger: 'change' }
      ]
    },

    // 
    statusOption:[
      {label:'',value:''},
      {label:'',value:''},
      {label:'',value:''}
    ],

    //
    teaVarieties: [
      { label: '', value: '' },
      { label: '', value: '' },
      { label: '', value: '' },
      { label: '', value: '' },
      { label: '', value: '' },
      { label: '', value: '' },
      { label: '', value: '' }
    ],
  }),
  actions:{

    // 
  async verifyLocation() {
    const { address, latitude, longitude } = this.formData;

    // 
    if (!address || !latitude || !longitude) {
      return { success: false, message: '' };
    }

    try {
      const apiKey = 'c89763ca3b15e5d916703381ed54d338';
      const encodedAddress = encodeURIComponent(address);
      const response = await fetch(
        `https://restapi.amap.com/v3/geocode/geo?address=${encodedAddress}&key=${apiKey}`
      );

      if (!response.ok) {
        throw new Error('');
      }

      const result = await response.json();
      // API
      if (result.status !== '1' || result.geocodes.length === 0) {
        return {
          success: false,
          message: ''
        };
      }

      // API
      const [apiLongitude, apiLatitude] = result.geocodes[0].location.split(',').map(Number);

      // 
      const latDiff = Math.abs(latitude - apiLatitude);
      const lngDiff = Math.abs(longitude - apiLongitude);
      const totalDiff = Math.sqrt(latDiff * latDiff + lngDiff * lngDiff);
      const distance = Math.round(totalDiff * 111); // 

      // 
      if (totalDiff < 0.01) { // 1
        return {
          success: true,
          message: `${distance}`
        };
      } else if (totalDiff < 0.1) { // 10
        return {
          success: false,
          warning: true,
          message: `${distance}`,
          suggestedLat: apiLatitude,
          suggestedLng: apiLongitude
        };
      } else { // 
        return {
          success: false,
          message: `${distance}`,
          suggestedLat: apiLatitude,
          suggestedLng: apiLongitude
        };
      }
    } catch (error) {
      return {
        success: false,
        message: `${error.message}`
      };
    }
  },

  // 
    openAddForm() {
      this.resetForm()
      this.isEditMode = false
      this.dialogvisible = true
    },
  // 
  openEditForm(row){
    this.resetForm()
    this.isEditMode=true
    this.currentEditingId=row.id
    this.formData={...row}
    this.dialogvisible=true
  },
  // 
  submitForm(){
    if(this.isEditMode){
      const index = this.gardenList.findIndex(item=>item.id===this.currentEditingId)
      if(index!=-1){
        this.gardenList[index]={...this.gardenList[index],...this.formData}
      }
      return {success:true,message:''}
    }else{
      const newId = String(Math.max(...this.gardenList.map(item => Number(item.id)), 0) + 1)
      this.gardenList.push({
        id:newId,
        ...this.formData
      })
      return { success: true, message: '' }
    }
  },
  // 
  resetForm(){
    this.formData={
      name: '',
      address: '',
      latitude: null,
      longitude: null,
      area: null,
      variety: '',
      plantingYear: null,
      owner: '',
      status: ''
    }
    this.currentEditingId=null
  },
  // 
    handleDelete() {
      this.gardenList = this.gardenList.filter(item => item.id !== this.currentEditingId)
      this.closeDialog()
      return { success: true, message: '' }
    },
  // 
  closeDialog(){
    this.resetForm()
    this.dialogvisible=false
  },
  // 
    loadData() {
      this.tableLoading = true
      setTimeout(() => {
        this.tableLoading = false
      }, 800)
    }
  },
  getters: {
    dialogTitle() {
      return this.isEditMode ? '' : ''
    }
  }
})
