<script>
import { ref, onMounted, computed, nextTick } from 'vue';
import Chart from 'chart.js/auto'; // 

export default {
  setup() {
    // 
    const loading = ref(true);
    const userInfo = ref({});
    const defaultAvatar = 'https://i.pravatar.cc/300';
    const defaultPlantationImg = 'https://picsum.photos/400/200?grayscale';
    const growthChart = ref(null); // canvas ref

    // 
    const roleMap = {
      'group': '',
      'subcompany': '',
      'farmer': ''
    };

    //  - 
    const totalDrones = computed(() => {
      return userInfo.value.plantations?.reduce((sum, p) => sum + p.droneCount, 0) || 0;
    });

    const totalSensors = computed(() => {
      return userInfo.value.plantations?.reduce((sum, p) => sum + p.sensorCount, 0) || 0;
    });

    const onlineSensors = computed(() => {
      return userInfo.value.plantations?.reduce((sum, p) => sum + p.onlineSensors, 0) || 0;
    });

    const runningDrones = computed(() => {
      return userInfo.value.plantations?.reduce((sum, p) => sum + p.runningDrones, 0) || 0;
    });

    // 
    const authStatusText = computed(() => {
      const status = userInfo.value.auth?.status;
      const map = {
        'verified': '',
        'pending': '',
        'rejected': '',
        'expired': ''
      };
      return map[status] || '';
    });

    // 
    const authStatusClass = computed(() => {
      const status = userInfo.value.auth?.status;
      const map = {
        'verified': 'auth-success',
        'pending': 'auth-pending',
        'rejected': 'auth-failed',
        'expired': 'auth-expired'
      };
      return map[status] || 'auth-none';
    });

    // ID
    const getDevicesByPlantation = (plantationId) => {
      return userInfo.value.devices?.filter(device => device.plantationId === plantationId) || [];
    };

    // 
    const handleViewAllPlantations = () => {
      // 
      console.log('');
      // this.$router.push('/all-plantations');
    };

    // 
    const fetchData = async () => {
      // API
      await new Promise(resolve => setTimeout(resolve, 1500));

      // 
      userInfo.value = {
        userId: 'AGR2025001',
        nickname: '',
        username: 'tea_farmer',
        avatar: 'https://i.pravatar.cc/300?img=23',
        phone: '138****6789',
        onlineStatus: 'online',
        updatedAt: '2025-08-10T14:30:00',
        lastLogin: {
          time: '2025-08-12T08:45:00',
          location: '-'
        },
        loginDevices: [
          { id: 'd1', name: 'Chrome', os: 'Windows 10', isCurrent: true },
          { id: 'd2', name: 'iPhone', os: 'iOS 17', isCurrent: false }
        ],
        roles: [
          { code: 'group', name: '' },
          { code: 'farmer', name: '' }
        ],
        currentRole: 'farmer',
        auth: {
          status: 'verified',
          type: '',
          progress: 100,
          expireWarning: true,
          expireDays: 30
        },
        todayWorkArea: 256,
        abnormalEvents: 3,
        plantations: [
          {
            id: 'p1',
            name: '',
            imageUrl: 'https://picsum.photos/400/200?random=1',
            province: '',
            city: '',
            droneCount: 3,
            runningDrones: 2,
            sensorCount: 12,
            onlineSensors: 11,
            area: 150,
            status: 2, // 
            pestStatus: 0 // 
          },
          {
            id: 'p2',
            name: '',
            imageUrl: 'https://picsum.photos/400/200?random=2',
            province: '',
            city: '',
            droneCount: 2,
            runningDrones: 1,
            sensorCount: 8,
            onlineSensors: 8,
            area: 200,
            status: 2,
            pestStatus: 1 // 
          }
        ],
        devices: [
          {
            id: 'dev1',
            type: 'drone',
            name: 'DJI T60',
            status: 'online',
            firmwareVersion: 'v2.3.0',
            needsUpgrade: false,
            plantationId: 'p1' // 
          },
          {
            id: 'dev2',
            type: 'sensor',
            name: '',
            status: 'online',
            firmwareVersion: 'v1.5.2',
            needsUpgrade: true,
            plantationId: 'p1' // 
          },
          {
            id: 'dev3',
            type: 'gateway',
            name: '',
            status: 'offline',
            firmwareVersion: 'v3.1.0',
            needsUpgrade: false,
            plantationId: 'p2' // 
          },
          {
            id: 'dev4',
            type: 'drone',
            name: 'DJI M300',
            status: 'online',
            firmwareVersion: 'v2.1.0',
            needsUpgrade: true,
            plantationId: 'p2' // 
          }
        ],
        inventory: [
          { id: 'i1', type: '', quantity: 50, threshold: 30, unit: 'kg' },
          { id: 'i2', type: '', quantity: 25, threshold: 40, unit: '' },
          { id: 'i3', type: '', quantity: 8, threshold: 10, unit: '' }
        ]
      };

      loading.value = false;
      // DOM
      await nextTick();
      initGrowthChart();
    };

    // 
    const initGrowthChart = () => {
      console.log('Initializing growth chart...');
      console.log('growthChart ref:', growthChart.value);

      if (!growthChart.value) {
        console.warn('Canvas element not found, retrying in 100ms...');
        setTimeout(initGrowthChart, 100);
        return;
      }

      try {
        const ctx = growthChart.value.getContext('2d');
        new Chart(ctx, {
          type: 'line',
          data: {
            labels: ['1', '2', '3', '4', '5', '6'],
            datasets: userInfo.value.plantations.map((p, index) => ({
              label: p.name,
              data: [
                Math.random() * 30 + 50,
                Math.random() * 30 + 60,
                Math.random() * 30 + 70,
                Math.random() * 30 + 80,
                Math.random() * 30 + 85,
                Math.random() * 30 + 90
              ],
              borderColor: index === 0 ? '#3e95cd' : '#8e5ea2',
              tension: 0.3,
              fill: false
            }))
          },
          options: {
            responsive: true,
            plugins: {
              legend: { position: 'top' },
              title: { display: true, text: '' }
            },
            scales: {
              y: { beginAtZero: false, min: 50 }
            }
          }
        });
      } catch (error) {
        console.error('Failed to initialize chart:', error);
      }
    };

    //  - 
    const handleAvatarError = (e) => {
      e.target.src = defaultAvatar;
    };

    //  - 
    const handleAvatarSelect = (e) => {
      const file = e.target.files[0];
      if (file) {
        // 
        const reader = new FileReader();
        reader.onload = (event) => {
          userInfo.value.avatar = event.target.result;
          // uploadAvatar(file)
        };
        reader.readAsDataURL(file);
      }
    };

    //  - 
    const handleRoleChange = async (e) => {
      const newRole = e.target.value;
      // APIJWT+Redis
      // await api.switchRole(newRole);
      userInfo.value.currentRole = newRole;
    };

    //  - 
    const handleForceLogout = async (deviceId) => {
      if (confirm('')) {
        // API
        // await api.forceLogout(deviceId);
        userInfo.value.loginDevices = userInfo.value.loginDevices.filter(d => d.id !== deviceId);
      }
    };

    //  - 
    const formatDate = (dateStr) => {
      if (!dateStr) return '';
      return new Date(dateStr).toLocaleString('zh-CN', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit'
      });
    };

    //  - 
    const deployStatusText = (status) => {
      return ['', '', ''][status] || '';
    };

    //  - 
    const pestStatusText = (status) => {
      return ['', '', '', ''][status] || '';
    };

    //  - 
    const badgeClass = (status, type) => {
      if (type === 'deploy') {
        return ['status-pending', 'status-processing', 'status-success'][status] || '';
      }
      return ['pest-none', 'pest-minor', 'pest-medium', 'pest-severe'][status] || '';
    };

    // 
    onMounted(fetchData);

    return {
      loading,
      userInfo,
      defaultAvatar,
      defaultPlantationImg,
      growthChart, // canvas ref
      roleMap,
      totalDrones,
      totalSensors,
      onlineSensors,
      runningDrones,
      authStatusText,
      authStatusClass,
      handleAvatarError,
      handleAvatarSelect,
      handleRoleChange,
      handleForceLogout,
      formatDate,
      deployStatusText,
      pestStatusText,
      getDevicesByPlantation,
      handleViewAllPlantations,
      badgeClass
    };
  }
};
</script>

