<script setup>
import { useGardenStore } from '@/stores/gardenStore';
import { ElLoading,ElMessageBox,ElMessage } from 'element-plus';
import { ref,onMounted } from 'vue';
const gardenStore=useGardenStore()
const gardenForm=ref(null);

onMounted(() => {
  gardenStore.loadData();
});
// 
const verifyLocation = async () => {
  const loading = ElLoading.service({
    lock: true,
    text: '...',
    background: 'rgba(0, 0, 0, 0.7)'
  });
  try {
    const result = await gardenStore.verifyLocation();
    if (!result.success && result.suggestedLat && result.suggestedLng) {
      //  ElMessageBox.confirm 
      const action = await ElMessageBox.confirm(
        `${result.message}(:${result.suggestedLat},:${result.suggestedLng})`,
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: result.warning ? 'warning' : 'error',
        }
      );
      //  action 
      if (action === 'confirm') {
        gardenStore.$patch(state => {
          state.formData.latitude = result.suggestedLat;
          state.formData.longitude = result.suggestedLng;
        });
      }
    } else if (result.success) {
      ElMessage.success(result.message);
    } else {
      ElMessage.error(result.message);
    }
  } catch (error) {
    //  ElMessageBox.confirm 
    console.error('', error);
  } finally {
    loading.close();
  }
};
// 
const handleDeleteConfirm = () => {
  ElMessageBox.confirm(
    '',
    '',
    {
      confirmButtonText: '',
      cancelButtonText: '',
      type: 'warning'
    }
  )
    .then(() => {
      const loading = ElLoading.service({
        lock: true,
        text: '...',
        background: 'rgba(0, 0, 0, 0.7)'
      });

      setTimeout(() => {
        loading.close();
        const result = gardenStore.handleDelete()
        ElMessage.success(result.message)
      }, 1000);
    })
    .catch(() => {
      ElMessage.info('');
    });
};
// 
const handleSubmit=()=>{
  gardenForm.value.validate((valid)=>{
    if(!valid){
      return false;
    }
    const loading = ElLoading.service({
      lock:true,
      text:'...',
      background:'rgba(0,0,0,0.7)'
    });

    setTimeout(()=>{
      loading.close();
      const result = gardenStore.submitForm()
      if(result.success){
        ElMessage.success(result.message)
      }else{
        ElMessage.error('')
      }
      gardenStore.closeDialog()
    },1000)
  })
}
// 
const currentPage = ref(1);
const handleCurrentChange = (val) => {
  currentPage.value = val;
};
</script>

<template>
  <div class="gardenform">
    <el-card title="">
    <el-table
    :data="gardenStore.gardenList.slice((currentPage - 1) * 10, currentPage * 10)"
    border
    :loading="gardenStore.tableLoading"
    stripe
    style="width: 1300px;"
    >
    <el-table-column label="" width="70">
      <template #default="scope">
        {{ (currentPage - 1) * 10 + scope.$index + 1 }}
      </template>
    </el-table-column>
    <el-table-column prop="name" label="" width="120" />
    <el-table-column prop="address" label="" width="250" />
    <el-table-column prop="status" label="" width="120">
      <template #default="scope">
        <el-tag
        :type="scope.row.status===''?'success':scope.row.status===''?'info':'warning'">
        {{scope.row.status}}
        </el-tag>
      </template>
    </el-table-column>
     <el-table-column prop="latitude" label="(N)"/>
    <el-table-column prop="longitude" label="(E)"/>
    <el-table-column prop="area" label="()"/>
    <el-table-column prop="variety" label="" />
    <el-table-column prop="plantingYear" label=""/>
    <el-table-column prop="owner" label="" width="180" />
    <el-table-column label="" width="100">
      <template #default="scope">
        <el-button size="small" type="primary" @click="gardenStore.openEditForm(scope.row)">
          
        </el-button>
      </template>
    </el-table-column>
    </el-table>
    <!--  -->
     <el-dialog
     :title="gardenStore.dialogTitle"
     v-model="gardenStore.dialogvisible"
     width="600px"
     :before-close="gardenStore.closeDialog"
     >
     <el-form
     ref="gardenForm"
     :model="gardenStore.formData"
     :rules="gardenStore.formRules"
     label-width="120px"
     >
     <el-form-item label="" prop="name">
        <el-input
        v-model="gardenStore.formData.name"
        placeholder=""
        maxlength="30"
        />
     </el-form-item>
     <el-form-item label="" prop="address">
      <el-input
      v-model="gardenStore.formData.address"
      placeholder=""
      maxlength="60"
      />
     </el-form-item>
     <el-form-item label="" prop="status">
        <el-select v-model="gardenStore.formData.status" placeholder="">
          <el-option
          v-for="item in gardenStore.statusOption"
          :key="item.value"
          :label="item.label"
          :value="item.value"
          ></el-option>
        </el-select>
     </el-form-item>
     <el-form-item label="" prop="coordinates">
        <div class="coordinates-input">
          <el-input-number
            v-model="gardenStore.formData.latitude"
            :min="-90"
            :max="90"
            :step="0.000001"
            placeholder=""
            style="width: 48%"
            controls-position="right"
          />
          <el-input-number
            v-model="gardenStore.formData.longitude"
            :min="-180"
            :max="180"
            :step="0.000001"
            placeholder=""
            style="width: 48%"
            controls-position="right"
          />
        </div>
        <el-button
          type="primary"
          size="small"
          @click="verifyLocation"
        >
          <el-icon><Location /></el-icon>
          
        </el-button>
      </el-form-item>

      <el-form-item label="()" prop="area">
        <el-input-number
          v-model="gardenStore.formData.area"
          :min="0.1"
          :max="10000"
          :step="0.1"
          placeholder=""
          controls-position="right"
        />
      </el-form-item>

      <el-form-item label="" prop="variety">
        <el-select
          v-model="gardenStore.formData.variety"
          placeholder=""
          filterable
          allow-create
        >
          <el-option
            v-for="item in gardenStore.teaVarieties"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          />
        </el-select>
      </el-form-item>

      <el-form-item label="" prop="plantingYear">
        <el-input-number
          v-model="gardenStore.formData.plantingYear"
          :min="1950"
          :max="new Date().getFullYear()"
          placeholder=""
          controls-position="right"
        />
      </el-form-item>

      <el-form-item label="" prop="owner">
        <el-input
          v-model="gardenStore.formData.owner"
          placeholder=""
          maxlength="100"
        />
      </el-form-item>
     </el-form>
     <template #footer>
     <el-button @click="gardenStore.closeDialog"></el-button>
     <!--  -->
     <template v-if="gardenStore.isEditMode">
      <el-button
      type="danger"
      @click="handleDeleteConfirm"
      >
        <el-icon><Delete /></el-icon>
        
      </el-button>
     </template>
     <!--  -->
     <template v-else>
      <el-button
      type="info"
      @click="gardenStore.resetForm"
      >
      <el-icon><Refresh /></el-icon>
      
      </el-button>
     </template>
     <el-button type="primary" @click="handleSubmit">
     <el-icon><Check /></el-icon>
     
     </el-button>
     </template>
    </el-dialog>
    </el-card>
    <br>
    <!--  -->
  <el-pagination
  layout="prev, pager, next, jumper, total"
  :page-size="10"
  :current-page="currentPage"
  :total="gardenStore.gardenList.length"
  @current-change="handleCurrentChange"
  :locale="paginationLocale"    />
  </div>
</template>

<style scoped>
.gardenform{
position: relative;
left: -60px;
}
</style>
